<template>
  <div class="console-wrapper">
    <div class="console-operation">
      <!--
        <div class="revert-container">
         <n-icon size="24" @click="revert">
          <return-up-back-outline />
        </n-icon> 
      </div>
      -->
      <div class="btn-container">
        <n-button type="error" @click="reset"></n-button>
        <n-button type="info" @click="download"></n-button>
      </div>
    </div>
    <div class="console-container">
      <template v-if="componentStyle.length > 0">
        <div
          class="console-component-item"
          v-for="(component, index) in componentStyle"
          :key="component.varName + index"
        >
          <ConsoleColorPicker
            v-if="component.component === 'n-color-picker'"
            :index="index"
            :var-name="component.varName"
            :var-value="component.varValue"
          />

          <ConsoleInput
            v-if="component.component === 'n-input'"
            :index="index"
            :var-name="component.varName"
            :var-value="component.varValue"
          />
        </div>
      </template>
      <div class="console-component-no-data-text" v-else></div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, watch, onUnmounted } from 'vue'
import { useStore } from 'vuex'
import { NButton, useDialog, useMessage } from 'naive-ui'
import ConsoleColorPicker from '../components/ConsoleColorPicker.vue'
import ConsoleInput from '../components/ConsoleInput.vue'

export default defineComponent({
  name: 'PreviewConsole',
  components: {
    NButton,
    ConsoleColorPicker,
    ConsoleInput
  },
  setup() {
    const $store = useStore()
    const dialog = useDialog()
    const message = useMessage()
    const componentStyle = computed(() => $store.state.componentConsoleStyle)
    const menuActive = computed(() => $store.state.menuActive)
    const revert = () => {
      message.info('')
    }
    const reset = () => {
      dialog.error({
        title: '',
        content: '~',
        positiveText: '',
        negativeText: '',
        maskClosable: false,
        onPositiveClick: () => {
          $store.dispatch('resetTheme')
          message.success('')
        }
      })
    }
    const download = async () => {
      const currentThemeInfo = await $store.dispatch('getCurrentThemeInfo')
      $store.dispatch('downloadTheme', currentThemeInfo)
      message.success('')
    }
    const stopWatchMenuActive = watch(
      menuActive,
      newState => {
        $store.dispatch('getComponentConsoleStyle', newState)
      },
      { immediate: true }
    )
    onUnmounted(() => {
      stopWatchMenuActive()
    })
    return {
      componentStyle,
      revert,
      reset,
      download
    }
  }
})
</script>
